import { ChangeDetectionStrategy, Component, Directive } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { AccountStatement } from '@backbase/data-ang/account-statements';
import { AccountStatementStoreModel, SortingParams, PaginationParams } from '@backbase/account-statement-store-ang';
import { first } from 'rxjs/operators';
import { AccountStatementBusinessPropertiesService } from '../../services/account-statement-business-properties.service';
import { LoadingState, PaginationType } from '@backbase/account-statement-common-ang';
import { LoadMoreParams } from '@backbase/ui-ang/load-button';
import { SortEvent } from '@backbase/ui-ang/table';

@Component({
  selector: 'bb-account-statement-business-statements-container',
  templateUrl: './account-statement-business-statements-container.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AccountStatementBusinessStatementsContainerComponent {
  readonly accountStatements$ = this.store.accountStatements$;
  readonly totalCount$ = this.store.accountStatementsTotalCount$;
  readonly accountStatementsLoading$ = this.store.accountStatementsLoading$;
  readonly sortingParams$ = this.store.accountStatementsSortingParams$;
  readonly paginationParams$ = this.store.accountStatementsPaginationParams$;
  readonly loadingStates = LoadingState;
  readonly paginationTypes = PaginationType;
  readonly paginationType$ = this.propertiesService.paginationType$;
  readonly showCategory$ = this.propertiesService.showCategory$;
  private readonly pageSize$ = this.propertiesService.pageSize$.pipe(first());

  constructor(
    private readonly store: AccountStatementStoreModel,
    private readonly propertiesService: AccountStatementBusinessPropertiesService,
  ) {}

  loadMore(params: LoadMoreParams) {
    this.pageSize$.subscribe(size => {
      this.store.dispatchLoadAccountStatementsBusiness({
        paginationParams: {
          size,
          from: params.from,
        },
        append: true,
      });
    });
  }

  onSort(sortEvent: SortEvent) {
    this.pageSize$.subscribe(size => {
      this.store.dispatchLoadAccountStatementsBusiness({
        sortingParams: sortEvent,
        paginationParams: {
          size,
          from: 0,
        },
      });
    });
  }

  handlePageChange(page: number) {
    this.pageSize$.subscribe(size => {
      this.store.dispatchLoadAccountStatementsBusiness({
        paginationParams: {
          size,
          from: page,
        },
      });
    });
  }
}

@Directive({ selector: '[bbAccountStatementBusinessStatementsContainerCustomizable]' })
export class AccountStatementBusinessStatementsContainerCustomizableDirective extends BbTemplate<
  AccountStatementBusinessStatementsContainerComponent,
  {
    accountStatements: AccountStatement[];
    totalCount: number;
    accountStatementsLoading: LoadingState;
    loadingStates: typeof LoadingState;
    paginationParams: PaginationParams;
    sortingParams: SortingParams;
    paginationType: PaginationType;
    showCategory: boolean;
    paginationTypes: typeof PaginationType;
  }
> {}
