import { ActivatedRoute, ParamMap } from '@angular/router';

import { Observable } from 'rxjs';
import { pluck } from 'rxjs/operators';

import { ProductSummaryItem } from '@backbase/data-ang/arrangements';

/**
 * Extract the given param from activated route.
 *
 * @param route of type ActivatedRoute
 * @param param URL param that needs to be get from the active route
 * @returns Observable of extracted param
 */
export const getRouteParam = (route: ActivatedRoute, param: string): Observable<string | undefined> =>
  route.paramMap.pipe(pluck<ParamMap, string | undefined>('params', param));

/**
 * Check if the given object is of type ProductSummaryItem or not
 *
 * @param account Either ProductSummaryItem or string
 * @returns boolean
 */
export const isProductsummaryItem = (account: ProductSummaryItem | string): account is ProductSummaryItem =>
  (account as ProductSummaryItem).id !== undefined;

/**
 * Check if the passed parameter is ProductSummaryItem array or accountId's array and return id from it
 *
 * @param accounts Array of either ProductSummaryItem or string
 * @returns string accountId
 */
export const getAccountId = (accounts: ProductSummaryItem[] | string[]): string =>
  isProductsummaryItem(accounts[0]) ? accounts[0].id : accounts[0];
