import { Injectable, Optional } from '@angular/core';
import { Observable, of } from 'rxjs';

import { ItemModel } from '@backbase/foundation-ang/core';
import { AccountStatementBasePropertiesService, PaginationType } from '@backbase/account-statement-common-ang';

@Injectable()
export class AccountStatementBusinessPropertiesService extends AccountStatementBasePropertiesService {
  constructor(@Optional() itemModel: ItemModel) {
    super(itemModel);
  }

  protected getPaginationTypeIM$(): Observable<PaginationType> {
    const defaultValue = PaginationType.Pagination;
    return this.itemModel?.property('paginationType', defaultValue) || of(defaultValue);
  }
}
