import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { AccountStatementDocument } from '@backbase/data-ang/account-statements';

@Component({
  selector: 'bb-account-statement-view-online-business-wrapper',
  templateUrl: './account-statement-view-online-business-wrapper.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AccountStatementViewOnlineBusinessWrapperComponent {
  @Input()
  set documents(documents: AccountStatementDocument[]) {
    this.documentPDF = documents.find(document => document.contentType === 'application/pdf');
  }

  @Input() describedByElementId = '';

  documentPDF?: AccountStatementDocument;
}
