import { Component, Directive, Input, OnChanges, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import {
  AccountStatementBaseWidgetComponent,
  DatePeriods,
  getDateFilterLimit,
  LoadingState,
  stringToWords,
  today,
} from '@backbase/account-statement-common-ang';
import { AccountStatementsFilterParams, AccountStatementStoreModel } from '@backbase/account-statement-store-ang';
import { AccountStatement } from '@backbase/data-ang/account-statements';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { combineLatest, Observable, Subject, BehaviorSubject, merge } from 'rxjs';
import { filter, first, map, takeUntil } from 'rxjs/operators';
import { AccountStatementBusinessPropertiesService } from '../../services/account-statement-business-properties.service';
import { getAccountId, getRouteParam } from '../../helpers/utils';
import { SortDirection } from '@backbase/ui-ang/table';

@Component({
  selector: 'bb-account-statement-business-widget',
  templateUrl: './account-statement-business-widget.component.html',
  providers: [AccountStatementStoreModel, AccountStatementBusinessPropertiesService],
})
export class AccountStatementBusinessWidgetComponent
  extends AccountStatementBaseWidgetComponent<AccountStatementBusinessPropertiesService>
  implements OnInit, OnDestroy, OnChanges
{
  @Input() accountId = '';
  readonly accountStatements$: Observable<AccountStatement[]> = this.store.accountStatements$;
  readonly accountStatementsLoading$: Observable<LoadingState> = this.store.accountStatementsLoading$;
  readonly accountStatementsError$: Observable<Error | undefined> = this.store.accountStatementsError$;
  readonly pageSize$: Observable<number> = this.propertiesService.pageSize$.pipe(first());
  readonly accountStatementsFilterParams$: Observable<AccountStatementsFilterParams> =
    this.store.accountStatementsFilterParams$;
  readonly loadingStates = LoadingState;

  private readonly destroy$ = new Subject<void>();
  private accountId$ = new BehaviorSubject<string>('');

  // In order to be able to transmit an account ids both using the route parameter and through the input
  // eslint-disable-next-line @typescript-eslint/member-ordering
  readonly accountIds$: Observable<string[]> = merge(
    getRouteParam(this.route, 'selectedAccount'),
    this.accountId$.pipe(filter(accountId => !!accountId)),
  ).pipe(map(accountIds => stringToWords(accountIds, ',')));

  constructor(
    private readonly route: ActivatedRoute,
    private readonly store: AccountStatementStoreModel,
    propertiesService: AccountStatementBusinessPropertiesService,
  ) {
    super(propertiesService);
  }

  ngOnChanges() {
    if (this.accountId) {
      this.accountId$.next(this.accountId);
    }
  }

  ngOnInit() {
    this.initializeMinimalDateFilterLimit.pipe(takeUntil(this.destroy$)).subscribe(minimalDate => {
      const widgetPreferences = { minimalDate, maximumDate: today() };
      this.store.dispatchUpdateWidgetPreferences({ widgetPreferences });
    });

    const sortingParams$ = combineLatest([
      this.propertiesService.sortingOrder$,
      this.propertiesService.sortingDirection$.pipe(map(direction => direction.toLowerCase() as SortDirection)),
    ]).pipe(
      first(),
      map(([column, direction]) => ({
        column,
        direction,
      })),
    );

    combineLatest([
      // fetch statements if exactly one account ID is provided
      this.accountIds$.pipe(filter(({ length }) => length === 1)),
      this.pageSize$,
      sortingParams$,
    ])
      .pipe(takeUntil(this.destroy$))
      .subscribe(([accounts, size, sortingParams]) => {
        this.store.dispatchLoadAccountStatementsBusiness({
          accountIds: [getAccountId(accounts)],
          sortingParams,
          paginationParams: {
            from: 0,
            size,
          },
          // reset filters for date
          filterParams: {
            datePeriod: DatePeriods.AllTime,
          },
        });
      });

    this.propertiesService.showCategory$
      .pipe(filter(Boolean), takeUntil(this.destroy$))
      .subscribe(_ => this.store.dispatchLoadCategories());
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  private get initializeMinimalDateFilterLimit(): Observable<Date> {
    return combineLatest([this.propertiesService.absoluteDateLimit$, this.propertiesService.relativeDateLimit$]).pipe(
      map(([absoluteDateLimit, relativeMonthLimit]) => getDateFilterLimit(absoluteDateLimit, relativeMonthLimit)),
    );
  }
}

@Directive({ selector: '[bbAccountStatementBusinessWidgetCustomizable]' })
export class AccountStatementBusinessWidgetCustomizableDirective extends BbTemplate<
  AccountStatementBusinessWidgetComponent,
  {
    accountStatements: AccountStatement[];
    accountStatementsLoading: LoadingState;
    accountStatementsError?: Error;
    accountStatementsFilterParams?: AccountStatementsFilterParams;
    loadingStates: typeof LoadingState;
    accountIds: string[];
  }
> {}
