import {
  ChangeDetectionStrategy,
  Component,
  Directive,
  EventEmitter,
  Input,
  Output,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import {
  AccountStatementDataService,
  MimeTypeExtensions,
  mimeTypeExtensions,
} from '@backbase/account-statement-common-ang';
import { SortingParams } from '@backbase/account-statement-store-ang';
import { AccountStatement, AccountStatementDocument } from '@backbase/data-ang/account-statements';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { AccountStatementBusinessPropertiesService } from '../../services/account-statement-business-properties.service';
import { NotificationService } from '@backbase/ui-ang/notification';
import { SortDirection, SortEvent } from '@backbase/ui-ang/table';

@Component({
  selector: 'bb-account-statement-business-table',
  templateUrl: './account-statement-business-table.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AccountStatementBusinessTableComponent {
  @Input()
  set accountStatements(accountStatements: AccountStatement[]) {
    this.accountStatementsValue = accountStatements.map(accountStatement => ({
      ...accountStatement,
      documents: accountStatement.documents.map(document => ({
        ...document,
        url: this.getDocumentLink(document),
      })),
    }));
  }
  get accountStatements(): AccountStatement[] {
    return this.accountStatementsValue;
  }

  @Input() sortingParams?: SortingParams;

  @Input() loading = false;

  @Input() showCategory = true;

  @Output() sort = new EventEmitter<SortEvent>();

  @ViewChild('downloadFailureNotificationHeader') downloadFailureNotificationHeader!: TemplateRef<any>;

  readonly mimeTypeExtensions = mimeTypeExtensions;

  private accountStatementsValue: AccountStatement[] = [];

  constructor(
    private readonly dataService: AccountStatementDataService,
    private readonly notificationsService: NotificationService,
    private readonly propertiesService: AccountStatementBusinessPropertiesService,
  ) {}

  onSort(sortEvent: SortEvent) {
    this.sort.emit(sortEvent);
  }

  onDownloadFailure() {
    this.propertiesService.notificationDismissTime$.subscribe(ttl => {
      this.notificationsService.showNotification({
        header: this.downloadFailureNotificationHeader,
        message: '',
        modifier: 'error',
        ttl,
      });
    });
  }

  getDirection(column: string): SortDirection {
    return this.sortingParams && this.sortingParams.column === column ? this.sortingParams.direction : '';
  }

  private getDocumentLink(document: AccountStatementDocument): string {
    return document.url || this.dataService.getAccountStatementDownloadUrl(document.uid || '');
  }
}

@Directive({ selector: '[bbAccountStatementBusinessTableCustomizable]' })
export class AccountStatementBusinessTableCustomizableDirective extends BbTemplate<
  AccountStatementBusinessTableComponent,
  {
    accountStatements: AccountStatement[];
    mimeTypeExtensions: MimeTypeExtensions;
    loading: boolean;
  }
> {}

@Directive({
  selector: '[bbAccountStatementBusinessDownloadFailureNotificationHeaderCustomizableDirective]',
})
export class AccountStatementBusinessDownloadFailureNotificationHeaderCustomizableDirective extends BbTemplate<
  AccountStatementBusinessTableComponent,
  void
> {}
